<template>
  <div id="app" class="container">
    <div class="todo">
      <h2 >Todo</h2>
    </div>
    <todo-list></todo-list>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'
import TodoList from './components/TodoList'

export default {
  name: 'App',
  components: {
    HelloWorld,
    TodoList
  }
}
</script>

<style>
* {
  box-sizing: border-box;
}
.container {
  max-width:600px;
  margin: 0 auto;
}
.todo {
  text-align: center;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 24px;
}
</style>



